import React from "react";
import NavSec from "./NavSec";
import Reveal from "react-reveal/Reveal";
import AboutInfo from "./AboutInfo";

const HomeMain = () => {
    return(
        <>
            <Reveal effect="animNav" duration="1000">
                <NavSec />                
            </Reveal>
            <AboutInfo />

        </>
    )
}

export default HomeMain;