import React, {useState, useEffect} from "react";
import db from '../firebase/config';
import { Container, Row, Col } from "reactstrap";
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';

const SetupMain = () => {
    const [setups, setSetups] = useState([]);

    useEffect(() => {
        db.collection("setup").onSnapshot((snapshot) =>
            setSetups(snapshot.docs.map((doc) => doc.data()))
        )
    }, [])

    return(
        <div className="setupMain">
            {setups.map((setup) => (
                <div className="setupImage">
                    <img src={setup.image} alt="setupimage" />
                </div>
            ))}
            <Container>
                <Row>
                    <Col md="11">
                        <div className="mySetup">
                            <span className="laptopSpecs">
                                <ul>
                                    <a href="#">
                                    <h2>HP </h2> <ArrowUpwardIcon />                                        
                                    </a>
                                    <li>
                                       CPU: Intel Core i3 - 10th Gen
                                    </li>
                                    <li>
                                       RAM: 8GB
                                    </li>
                                    <li>
                                       Storage: SSD 120GB, HDD 320GB
                                    </li>
                                    <li>
                                       Display: TFT, 60Hz
                                    </li>
                                    <li>
                                       GPU: INTEL UHD Graphics
                                    </li>

                                </ul>
                            </span>
                            <span className="laptopSpecs">
                                <ul>
                                    <a href="#">
                                    <h2>ASUS X509F</h2> <ArrowUpwardIcon />                                        
                                    </a>
                                    <li>
                                       CPU: Intel Core i3 - 10th Gen
                                    </li>
                                    <li>
                                       RAM: 8GB
                                    </li>
                                    <li>
                                       Storage: SSD 120GB, HDD 320GB
                                    </li>
                                    <li>
                                       Display: TFT 60Hz
                                    </li>
                                    <li>
                                       GPU: INTEL HD Graphics
                                    </li>

                                </ul>
                            </span>
                            <span className="keyboardSpecs">
                                <ul>
                                    <h2>Keyboard and Mouse</h2>
                                    <li>
                                        Keyboard: Integrated
                                        Mouse: Logitech G102
                                    </li>
                                </ul>
                            </span>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>

    )
}


export default SetupMain;