import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "reactstrap";
import db from '../firebase/config';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkIcon from '@material-ui/icons/Link';

const Project = () => {
    const [projects, setProjects] = useState([]);

    useEffect(() => {
        db.collection("project").onSnapshot((snapshot) =>
            setProjects(snapshot.docs.map((doc) => doc.data()))
        )
    }, [])

    return (
        <>
            <Container>
                <Row>
                    {projects.map((project, index) => (
                        <Col md="12" key={index}>
                            <div className="projectCard">
                                <img src={project.image} alt="portfolioimage" className="projectImage" />
                                <div className="projectTitles">
                                    <div className="projectAbout">
                                        <h3 className="projectName">{project.projectName}</h3>
                                        <p className="projectDesc">{project.projectDesc}</p>
                                    </div>
                                    <div className="projectButtons">
                                            <a href="#" target="blank" className="buttons">
                                                Source <GitHubIcon />
                                            </a>
                                            <a href="#" target="blank" className="buttons">
                                                Demo <LinkIcon />
                                            </a>
                                        </div>
                                </div>
                            </div>
                            <div className="projectCard projectCard_2">
                                <img src={project.image2} alt="portfolioimage" className="projectImage" />
                                <div className="projectTitles">
                                    <div className="projectAbout">
                                        <h3 className="projectName">{project.projectName}</h3>
                                        <p className="projectDesc">{project.projectDesc}</p>
                                    </div>
                                    <div className="projectButtons">
                                            <a href="#" target="blank" className="buttons">
                                                Source <GitHubIcon />
                                            </a>
                                            <a href="#" target="blank" className="buttons">
                                                Demo <LinkIcon />
                                            </a>
                                        </div>
                                </div>
                            </div>
                        </Col>
                    ))}

                </Row>
            </Container>
        </>
    )
};


export default Project;