import React, { useState, useEffect } from "react";
import Reveal from 'react-reveal/Reveal'
import db from "../firebase/config.js"
import { Link } from 'react-router-dom'
import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@mui/icons-material/GitHub';
import InstagramIcon from '@mui/icons-material/Instagram';
import TelegramIcon from '@mui/icons-material/Telegram';


const AboutInfo = () => {
    const[portfolios, setPortfolios] = useState([]);

    useEffect(() => {
        db.collection("portfolio").onSnapshot((snapshot) =>
            setPortfolios(snapshot.docs.map((doc) => doc.data()))
        )
    }, [])
    return(


        <div className="aboutinfo">
                 {portfolios.map(portfolio => (
                <>
                <div className="side2">
                    <img className="myImage" src={portfolio.image} alt="myimage" />
                </div>
                <div className="side1">
                    <Reveal effect="fadeInUp" duration="1000">
                        <h1 className="title">{portfolio.title}</h1>
                        <p className="description">{portfolio.description}</p>
                    </Reveal>
                    <div className="buttons">
                        <Link className="linkBtn" to="/About">About Me</Link>
                         <Link className="linkBtn" to="/Portfolio">Portfolio</Link>
                    </div>
                    <div className="socialNet">
                        <a className="links" href="https://www.youtube.com/channel/UClC2hC0U53Q5jk7S_tMnEjw" target="blank">
                            <FacebookIcon className="iconSvg" />
                        </a>
                        <a className="links" href="https://github.com/Abdurakhimow" target="blank">
                            <GitHubIcon className="iconSvg" />
                        </a>
                        <a className="links" href="https://www.instagram.com/abdurakhimow/" target="blank">
                            <InstagramIcon className="iconSvg" />
                        </a>
                        <a className="links" href="https://t.me/abdurakhimow" target="blank">
                            <TelegramIcon className="iconSvg" />
                        </a>
                   
                </div>
                </div>
                </>
            ))}
        </div>
           
    )
}

export default AboutInfo